.main{
  position: relative;
  overflow: hidden;
  .swiper{
    &-slide{
      background-size: cover;
      background-position: center;
    }
    &-button{
      position: static;
      margin: 0 0 0 12px;
      flex-shrink: 0;
    }
  }
  &__pag{
    display: flex;
    align-items: center;
    position: absolute;
    right: 66px;
    bottom: 50px;
  }
  &__inner{
    padding: 34.2% 0 4.3%;
  }
  &__content{
    padding: 24px;
    background: rgba(50, 50, 50, 0.66);
    border-left: 3px solid #FF441B;
    backdrop-filter: blur(3.5px);
    border-radius: 3px;
    max-width: 576px;
    color: #FFFFFF;
  }
  &__nav{
    margin-top: 32px;
    display: flex;
  }
  &__btn{
    padding: 0 18px;
    height: 59px;
    font-size: 24px;
    letter-spacing: -0.005em;
  }
}

.services{
  padding: 42px 0;
  margin-top: 30px;
  position: relative;
  .container{
    position: relative;
    z-index: 2;
  }
  &__bg{
    position: absolute;
    top: 0;
    right: 0;
  }
  &__title{
    text-align: center;
  }
  &__desc{
    text-align: center;
    margin: 14px auto 0;
    width: 708px;
    max-width: 100%;
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 15px));
    gap: 20px;
    margin-top: 40px;
  }
}

.advantages{
  padding: 42px 0;
  position: relative;
  z-index: 2;
  &__grid{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 15px));
    gap: 20px;
  }
  &__item{
    display: flex;
    align-items: center;
    font-size: 14px;
    line-height: 129.8%;
    letter-spacing: -0.005em;
  }
  &__num{
    font-weight: 700;
    font-size: 45px;
    line-height: 116%;
    color: $primary;
    flex-shrink: 0;
    margin-right: 12px;
    font-family: $font2;
  }
}

.about{
  padding: 42px 0;
  overflow: hidden;
  &__title{
    text-align: center;
  }
  &__grid {
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 10px));
    gap: 20px;
    margin-top: 40px;
  }
  &__left{
    max-width: 532px;
    position: relative;
    z-index: 3;
  }
  &__nav{
    display: flex;
    margin-top: 28px;
  }
  &__btn{
    font-size: 18px;
    letter-spacing: -0.03em;
    padding: 0 18px;
    height: 50px;
  }
  &__right{
    position: relative;
    &:before{
      content: '';
      position: absolute;
      width: 3000px;
      height: 100%;
      right: 100%;
      top: 0;
      background-color: #fff;
      z-index: 2;
    }
  }
  &__slider{
    position: relative;
    .main__pag{
      right: 12px;
      bottom: 12px;
    }
  }
  &__img{
    position: relative;
    padding-bottom: 83%;
    width: 100%;
    img{
      position: absolute;
      top: 0;
      left: 0;
      object-fit: cover;
      height: 100%;
      width: 100%;
      transition: all .3s;
    }
  }
  .swiper{
    &-button{
      position: static;
      margin: 0 0 0 12px;
      flex-shrink: 0;
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto ;
      display: flex;
      align-items: center;
      &-active+.swiper-slide{
        padding: 0 40px;
        .about__img{
          &:before{
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            z-index: 2;
            background: linear-gradient(0deg, rgba(217, 217, 217, 0.62), rgba(217, 217, 217, 0.62));
          }
        }
      }
    }
  }
}

.advantages2{
  padding: 85px 0;
  position: relative;
  &__bg{
    position: absolute;
    bottom: 0;
    left: 0;
  }
  .container{
    position: relative;
    z-index: 2;
  }
  &__title{
    text-align: center;
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 11.5px));
    gap: 23px;
    margin-top: 40px;
  }
}

.consult{
  background-image: url(../img/consult.jpg);
  background-size: cover;
  background-position: center;
  padding: 140px 0;
  &__inner{
    display: grid;
    grid-template-columns: 49.5% 42.5%;
    align-items: center;
    justify-content: space-between;
  }
  &__desc{
    margin-top: 24px;
  }
  &__btn{
    padding: 0 18px;
    height: 59px;
    margin-top: 16px;
    font-size: 24px;
    letter-spacing: -0.005em;
  }
}

.equip{
  padding: 70px 0;
  overflow: hidden;
  margin-top: 40px;
  &__title{
    text-align: center;
  }
  &__grid{
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin-top: 40px;
  }
  &__item{
    position: relative;
    padding: 24px 3px 24px 27px;
    line-height: inherit;
    &:before{
      content: '';
      width: 14.87px;
      height: 14.87px;
      position: absolute;
      top: 31px;
      left: 0;
      border-radius: 50%;
      background-color: $primary;
      z-index: 2;
    }
    p{
      position: relative;
      z-index: 2;
    }
    b{
      font-weight: 600;
    }
    &:after{
      content: '';
      height: 100%;
      top: 0;
      left: 50%;
      transform: translateX(-50%);
      width: 1000000px;
      background: #F7F7F7;
      position: absolute;
      display: none;
    }
    @media screen and (min-width: 993px){
      &:nth-child(5n - 4){
        &:after{
          display: block;
        }
      }
    }
  }
}

.co{
  padding: 42px 0;
  &__slider{
    display: grid;
    grid-template-columns: 42px calc(100% - 148px) 42px;
    gap: 32px;
    align-items: center;
  }
  .swiper{
    &-container{
      overflow: hidden;
    }
    &-button{
      position: static;
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
      padding: 0 32px;
      display: flex;
      align-items: center;
      justify-content: center;
      img{
        max-width: 100%;
        max-height: 100%;
      }
    }
    &-button{
      position: static;
      margin: 0;
    }
  }
}

.contacts{
  padding: 70px 0 110px;
  position: relative;
  overflow: hidden;
  &__bg{
    position: absolute;
    bottom: 0;
    left: 0;
  }
  .container{
    position: relative;
    z-index: 2;
  }
  &__title{
    text-align: center;
  }
  &__inner{
    display: grid;
    grid-template-columns: 334px auto;
    gap: 40px;
    margin-top: 40px;
  }
  &__left{
    background: rgba(239, 239, 239, 0.6);
    backdrop-filter: blur(2.5px);
    border-radius: 3px;
    padding: 32px;
  }
  .header__info{
    &-item{
      max-width: 100%;
      &:not(:last-child){
        margin-bottom: 30px;
      }
    }
    &-icon{
      margin-right: 6px;
    }
    &-content{
      font-size: 22px;
    }
  }
}

.map{
  width: 100%;
  height: 477px;
}


@media screen and (max-width: 992px){
  .main{
    &__inner{
      padding-bottom: 90px;
    }
    &__pag{
      right: 15px;
      bottom: 20px;
    }
    &__content{
      max-width: 100%;
    }
  }

  .services,
  .advantages{
    padding: 20px 0;
    &__grid{
      grid-template-columns: repeat(2,calc(50% - 10px));
    }
    &__bg{
      display: none;
    }
  }

  .about{
    padding: 20px 0;
    &__grid{
      grid-template-columns: 100%;
    }
    &__left{
      max-width: 100%;
    }
  }

  .advantages2{
    padding: 40px 0;
    &__bg{
      display: none;
    }
    &__grid{
      margin-top: 20px;
      grid-template-columns: 100%;
      gap: 15px;
    }
  }

  .consult{
    padding: 60px 0;
    &__inner{
      grid-template-columns: 100%;
      gap: 30px;
    }
  }

  .equip{
    padding: 20px 0;
    &__title{
      text-align: left;
    }
    &__grid{
      grid-template-columns: 100%;
      margin-top: 20px;
    }
    &__item{
      &:nth-child(odd){
        &:after{
          display: block;
        }
      }
    }
  }

  .co{
    padding: 20px 0;
  }

  .contacts{
    padding: 30px 0;
    &__bg{
      display: none;
    }
    &__inner{
      margin-top: 20px;
      grid-template-columns: 100%;
      gap: 20px;
    }
    &__left{
      padding: 16px;
    }
    .header__info{
      &-item{
        &:not(:last-child){
          margin-bottom: 20px;
        }
      }
      &-content{
        font-size: 18px;
      }
    }
  }

  .map{
    height: 250px;
  }
}


@media screen and (max-width: 576px){
  .main{
    &__content{
      padding: 16px;
    }
  }

  .services{
    &__grid{
      grid-template-columns: 100%;
      gap: 15px;
      margin-top: 20px;
    }
  }

  .advantages{
    &__grid {
      grid-template-columns: 100%;
    }
  }

  .consult{
    padding: 30px 0;
  }

  .equip{
    &__item{
      &:before{
        top: 27px;
      }
    }
  }
}
