.fg {
  position: relative;
  margin: 0 0 15px;
  label{
    font-size: 16px;
    line-height: 129.8%;
    letter-spacing: -0.005em;
    display: block;
    margin-bottom: 3px;
    span{
      color: $primary;
    }
  }
  input {
    border: 2px solid #fff;
    background: #FFFFFF;
    border-radius: 5px;
    padding: 0 20px;
    height: 63px;
    width: 100%;
    font-size: 18px;
    color: #000;
    &::placeholder {
      color: #000;
    }
    &:focus {
      border-color: $primary;
    }
  }
}

.m-check {
  position: relative;
  input {
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
    &:checked+label {
      &::before {
        border-width: 6px;
        border-color: $primary;
      }
    }
  }
  label {
    font-size: 14px;
    line-height: 129.8%;
    letter-spacing: -0.005em;
    cursor: pointer;
    display: flex;
    align-items: center;
    &:before {
      content: '';
      z-index: 1;
      border: 1px solid #F3F2F2;
      border-radius: 4px;
      background: #F3F2F2;
      width: 20px;
      height: 20px;
      display: block;
      flex-shrink: 0;
      margin-right: 6px;
    }
  }
}
