.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    &:focus {
        box-shadow: none;
    }
}

.m-btn-outline{
    border: 2px solid $primary;
    border-radius: 3px;
    color: $primary;
    font-weight: 600;
    &:hover{
        background-color: rgba($primary, .2);
        color: $primary;
    }
}

.m-btn-primary{
    color: #fff;
    background-color: $primary;
    border-radius: 3px;
    font-weight: 600;
    &:hover{
        color: #fff;
        background-color: darken($primary,10%);
    }
}

.swiper-button{
    background: #FFFFFF;
    box-shadow: 2px 4px 20px rgba(0, 0, 0, 0.07);
    border-radius: 6px;
    width: 44px;
    height: 44px;
}
