@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";

.swiper-pagination{
  width: unset;
  position: static;
  margin: 0 12px 0 0;
  display: flex;
  align-items: center;
  &-bullet{
    width: 8px;
    height: 8px;
    opacity: 1;
    background-color: #fff;
    margin: 0 3px !important;
    &-active{
      width: 42px;
      border-radius: 8px;
    }
  }
}
