.service-card{
  position: relative;
  &__img{
    position: relative;
    padding-bottom: 176%;
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      z-index: 2;
      background: linear-gradient(179.69deg, rgba(0, 0, 0, 0) 0.26%, #323232 99.73%);
      width: 100%;
      height: 100%;
    }
    img{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__title{
    width: 100%;
    position: absolute;
    left: 0;
    bottom: 0;
    padding: 16px 24px;
    text-align: center;
    z-index: 2;
    color: #fff;
  }
}

.advantages-card{
  padding: 33px;
  background: rgba(224, 224, 224, 0.4);
  backdrop-filter: blur(2px);
  &__top{
    display: flex;
    align-items: center;
  }
  &__icon{
    flex-shrink: 0;
    height: 60px;
    margin-right: 20px;
    img{
      height: 100%;
    }
  }
  &__title{
    font-weight: 500;
    font-size: 24px;
    line-height: 129.8%;
    letter-spacing: -0.005em;
  }
  &__desc{
    color: #666666;
    margin-top: 20px;
  }
}


@media screen and (max-width: 576px){
  .advantages-card{
    padding: 20px;
    &__icon{
      height: 45px;
      margin-right: 10px;
    }
    &__title{
      font-size: 18px;
    }
    &__desc{
      margin-top: 15px;
    }
  }
}
