.footer{
  background: #262626;
  color: #FFFFFF;
  &__top{
    display: flex;
    justify-content: space-between;
    padding: 50px 0 30px;
  }
  &__logo{
    display: block;
    width: 190px;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
  }
  &__links{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 10px;
    margin: 0 20px;
  }
  &__link,
  &__sublink{
    display: block;
    &:not(:last-child){
      margin-bottom: 32px;
    }
  }
  &__link{
    font-weight: 500;
    font-size: 24px;
    line-height: 129.8%;
  }
  &__sublink{
    font-size: 22px;
    line-height: 130%;
  }
  &__btn{
    flex-shrink: 0;
    padding: 0 18px;
    font-weight: 600;
    font-size: 18px;
    letter-spacing: -0.03em;
    height: 50px;
    border: 2px solid #FFFFFF;
    border-radius: 3px;
    color: #fff;
    &:hover{
      background-color: #fff;
      color: $primary;
    }
  }
  &__bottom{
    display: flex;
    justify-content: space-between;
    padding: 30px 0;
    border-top: 1px solid #434343;
  }
  &__c,
  &__by{
    font-size: 14px;
    line-height: 129.8%;
    letter-spacing: -0.005em;
  }
  &__by{
    display: flex;
    img{
      width: 41px;
      flex-shrink: 0;
      margin-right: 13px;
    }
  }
}


@media screen and (max-width: 992px){
  .footer{
    &__top{
      display: grid;
      grid-template-columns: 100%;
      gap: 30px;
    }
  }
}


@media screen and (max-width: 768px){
  .footer{
    &__logo{
      width: 120px;
    }
    &__links{
      grid-template-columns: 100%;
      gap: 15px;
    }
    &__link,
    &__sublink{
      display: block;
      &:not(:last-child){
        margin-bottom: 15px;
      }
    }
    &__link{
      font-size: 24px;
    }
    &__sublink{
      font-size: 18px;
    }
    &__bottom{
      display: grid;
      grid-template-columns: 100%;
      gap: 20px;
    }
  }
}
