.header{
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 27px 0;
  }
  &__logo{
    display: block;
    width: 153.57px;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
  }
  &__info{
    flex-grow: 1;
    margin: 0 40px;
    display: flex;
    justify-content: space-between;
    &-item{
      display: flex;
      max-width: 180px;
    }
    &-icon{
      width: 24px;
      flex-shrink: 0;
      margin-right: 3.91px;
      img{
        width: 100%;
      }
    }
    &-content{
      font-size: 14px;
      line-height: 129.8%;
      letter-spacing: -0.005em;
      a{
        display: block;
        line-height: inherit;
      }
    }
    &-title,
    &-text{
      line-height: inherit;
    }
    &-title,
    b{
      font-weight: 600;
    }
  }
  &__btn{
    flex-shrink: 0;
    width: 165px;
    height: 50px;
    font-size: 18px;
    letter-spacing: -0.03em;
  }
  &__bottom{
    padding: 25px 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-top: 1px solid $secondary;
  }
  &__socials{
    display: flex;
    align-items: center;
  }
  &__social{
    margin-left: 20px;
    width: 33px;
    display: block;
    &:hover{
      img{
        transform: translateY(-10px);
      }
    }
    img{
      width: 100%;
      transition: all .3s;
    }
  }
  &__burger{
    display: none;
  }
}

.nav{
  display: flex;
  align-items: center;
  &__link{
    display: flex;
    align-items: center;
    position: relative;
    text-transform: uppercase;
    line-height: 125%;
    font-size: 16px;
    &:not(:first-child){
      &:before{
        content: '';
        position: absolute;
        top: 50%;
        right: 100%;
        margin-right: 32px;
        transform: translate(50%,-50%);
        background-color: $secondary;
        width: 1px;
        height: 24px;
      }
    }
    &:not(:last-child){
      margin-right: 64px;
    }
    &--home{
      span{
        display: none;
      }
    }
  }
}


@media screen and (max-width: 1200px){
  .header{
    &__logo{
      width: 120px;
    }
    &__btn{
      width: 120px;
      height: 40px;
      font-size: 14px;
    }
    &__info{
      margin: 0 15px;
    }
  }
}


@media screen and (max-width: 992px){
  .header{
    &__top{
      justify-content: flex-end;
      padding: 15px 0;
      position: relative;
      z-index: 15;
    }
    &__logo{
      margin-right: auto;
    }
    &__info{
      display: none;
    }
    &__bottom{
      z-index: 14;
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100vh;
      background-color: #fff;
      opacity: 0;
      transition: all .3s;
      pointer-events: none;
      display: block;
      padding: 100px 15px 30px;
    }
    &__btn{
      background-image: url(../img/phone.svg);
      background-repeat: no-repeat;
      background-position: center;
      font-size: 0;
      width: 40px;
    }
    &__burger{
      display: flex;
      width: 40px;
      height: 40px;
      flex-shrink: 0;
      margin-left: 20px;
    }
    &._open-menu{
      .header{
        &__top{

        }
        &__bottom{
          opacity: 1;
          pointer-events: unset;
        }
      }
      .burger{
        span{
          &:nth-child(1){
            transform: rotate(-45deg) translate(-6px,6px);
          }
          &:nth-child(2){
            transform: scale(0);
          }
          &:nth-child(3){
            transform: rotate(45deg) translate(-6px,-6px);
          }
        }
      }
    }
    &__socials{
      margin-top: 40px;
    }
    &__social{
      margin: 0 20px 0 0;
    }
  }

  .nav{
    display: block;
    &__link{
      font-weight: 600;
      &:before{
        display: none !important;
      }
      &:not(:last-child){
        margin: 0 0 20px;
      }
      &--home{
        span{
          display: block;
        }
      }
      img{
        margin-right: 10px;
      }
    }
  }

  .burger{
    width: 24px;
    height: 20px;
    display: flex;
    align-items: center;
    position: relative;
    z-index: 15;
    span{
      display: block;
      width: 100%;
      height: 1.31px;
      background: $primary;
      left: 0;
      transition: all .3s;
      &:nth-child(1){
        position: absolute;
        top: 0;
      }
      &:nth-child(3){
        position: absolute;
        bottom: 0;
      }
    }
  }
}
