.display1{
  font-weight: 500;
  font-size: 40px;
  line-height: 116%;
}

.display2{
  font-weight: 600;
  font-size: 32px;
  line-height: 129.8%;
  letter-spacing: -0.005em;
}

.display3{
  font-weight: 600;
  font-size: 26px;
  line-height: 129.8%;
  letter-spacing: -0.005em;
}

.display-line{
  span{
    position: relative;
    &:before{
      content: '';
      position: absolute;
      top: 100%;
      left: 0;
      margin-top: 1px;
      width: 82.18px;
      height: 3px;
      background-color: $primary;
    }
  }
}

.text22{
  font-size: 22px;
  line-height: 130%;
  p{
    line-height: inherit;
    &:not(:last-child){
      margin-bottom: 20px;
    }
  }
}

.text14{
  font-size: 14px;
  line-height: 150%;
  letter-spacing: -0.005em;
}

.body-text{
  p{
    line-height: inherit;
    &:not(:last-child){
      margin-bottom: 10px;
    }
  }
}

.mlist{
  li{
    line-height: inherit;
    display: flex;
    &::before{
      content: '';
      display: block;
      width: 4px;
      height: 4px;
      border-radius: 50%;
      background-color: #000;
      transform: translateY(8px);
      margin-right: 5px;
      flex-shrink: 0;
    }
    &:not(:last-child){
      margin-bottom: 16px;
    }
  }
}


@media screen and (max-width: 576px){
  .display1{
    font-size: 27px;
  }

  .display2{
    font-size: 24px;
  }

  .display3{
    font-size: 20px;
  }

  .text22{
    font-size: 18px;
  }
}
